import React, { Component } from "react";
import "../app.scss";
import { NavLink } from "react-router-dom";
import { IoIosMenu } from "react-icons/io";

export const Nav = props => {
  let count = 0;

  const dropDown = () => {
    let showing = document.getElementById("nav-dropDown");

    if (window.getComputedStyle(showing).display === "none") {
      count = 1;
    }
    if (count == 0) {
      document.getElementById("nav-dropDown").style.display = "none";
      count = 1;
    } else {
      document.getElementById("nav-dropDown").style.display = "block";
      count = 0;
    }
  };
  return (
    <nav>
      <div className="nav">
        <IoIosMenu onClick={() => dropDown()} className="nav-bars" size={40} />
        <div className="logo-container">
          <NavLink id="logo-mobile" to="/">
            <h1>XolanisBlog</h1>
          </NavLink>
        </div>
        <div className="headers">
          <div className="dropDown_menu">
            <h2>Digital Access and Divide </h2>
            <div class="dropdown-content">
              <NavLink to="/nine-elements">
                <h3 className="dropdown-content-header">
                  Identifies Nine Elements
                </h3>
              </NavLink>
              <NavLink to="/social-capital">
                <h3 className="dropdown-content-header"> Social Capital</h3>
              </NavLink>
              <NavLink to="/linkedIn-account">
                <h3 className="dropdown-content-header"> LinkedIn</h3>
              </NavLink>
            </div>
          </div>
          <div className="dropDown_menu">
            <h2>Digital Literacy </h2>
            <div class="dropdown-content">
              <NavLink to="/kids">
                <h3 className="dropdown-content-header">
                  five characteristics of digital citizen
                </h3>
              </NavLink>
              <NavLink to="/digital-footprint">
                <h3 className="dropdown-content-header">Digital footprints </h3>
              </NavLink>
            </div>
          </div>

          <h2>Contact</h2>
        </div>
      </div>
      <div id="nav-dropDown">
        <NavLink to="/">
          <h2 className="nav-tabs">Home</h2>
        </NavLink>
        <NavLink to="/nine-elements">
          <h2 className="nav-tabs">Nine Elements</h2>
        </NavLink>
        <NavLink to="social-capital">
          <h2 className="nav-tabs">Social Capital</h2>
        </NavLink>
        <NavLink to="/linkedIn-account">
          <h2 className="nav-tabs">LinkedIn Account</h2>
        </NavLink>
        <NavLink to="/digital-footprint">
          <h2 className="nav-tabs">Digital footprints </h2>
        </NavLink>

        <h2 className="nav-tabs">
          The Five Characteristics Of Digital Citizen
        </h2>
      </div>
    </nav>
  );
};
