import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

import { store } from "./store";
import { Provider } from "react-redux";
import { Component } from "./component";

import { Footer } from "./Components/footer";
import { Error } from "./Components/error";
import { NineElements } from "./Components/nineElements";
import { SocialCapital } from "./Components/socialCapital";
import { DigitalFootPrint } from "./Components/digitalFootPrint";
import { LinkedIN } from "./Components/linkedInpage";
import { Nav } from "./Components/navigation";
import { BrowserRouter, Route, Switch } from "react-browser-router";
ReactDOM.render(
  <BrowserRouter>
    <div>
      <Nav />
      <Provider store={store}>
        <Switch>
          <Route path="/" component={Component} exact />
          <Route path="/nine-elements" component={NineElements} />
          <Route path="/social-capital" component={SocialCapital} />
          <Route path="/digital-footprint" component={DigitalFootPrint} />
          <Route path="/linkedIn-account" component={LinkedIN} />
          <Route component={Error} />
        </Switch>
      </Provider>
      <Footer />
    </div>
  </BrowserRouter>,
  document.getElementById("root")
);
