import React, { Component } from "react";
import "../app.scss";

export const LinkedIN = props => {
  const closeDrop = () => {
    document.getElementById("nav-dropDown").style.display = "none";
  };
  return (
    <div onClick={() => closeDrop()} id="page-coloure">
      <div className="image-linkedIn">
        <div className="headerPage">
          <h1>MY LINKEDIN ACCOUNT </h1>
        </div>
      </div>
      <div className="adult-main">
        <div className="mine-info1">
          <img
            id="linkedIn-img"
            src={require("../images/linkedind account.jpg")}
          />
          <br />
          <br />
          <h3>
            A link to my linkedIn account
            <a
              rel="stylesheet"
              href="https://www.linkedin.com/in/
xolani-yona-a16661154"
              target="_blank"
            >
              www.linkedin.com/in/ xolani-yona-a16661154
            </a>
          </h3>
        </div>
      </div>
    </div>
  );
};
